import { useEffect, useState } from "react";
import { FaFacebookF, FaTwitter, FaYoutube, FaLinkedinIn } from "react-icons/fa";

const Footer = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    // Simulated API data
    const res = {
      logo: "/logo.png",
      address: "C-20/1, Sector - 62, Noida, Uttar Pradesh",
      phone: "+91 9311830458",
      email: "admission@jssaten.ac.in",
      landlines: [
        "0120-2401448 (Direct)",
        "0120-2400115, 2401442, 2401449 (EPBX)"
      ],
      quickLinks: [
        { label: "Examination", url: "#" },
        { label: "Alumni", url: "#" },
        { label: "Annual Reports", url: "#" },
        { label: "ERP Login", url: "#" },
        { label: "Testimonials", url: "#" },
        { label: "Happenings", url: "#" },
        { label: "IQAC", url: "#" },
        { label: "Downloads", url: "#" },
        { label: "Careers", url: "#" },
        { label: "OBC Cell", url: "#" },
        { label: "National Digital Library", url: "#" },
        { label: "Online Grievance System", url: "#" }
      ],
      sections: [
        { title: "ABOUT JSS UNIVERSITY", links: [] },
        { title: "ACADEMICS", links: [] },
        { title: "ADMISSIONS", links: [] },
        { title: "FACILITIES", links: [] },
        { title: "STUDENT SUPPORT", links: [] },
        { title: "RESEARCH & INNOVATION", links: [] },
        { title: "PLACEMENTS", links: [] }
      ],
      socials: [
        { icon: "facebook", url: "#" },
        { icon: "twitter", url: "#" },
        { icon: "youtube", url: "#" },
        { icon: "linkedin", url: "#" }
      ]
    };

    setData(res);
  }, []);

  if (!data) return null;

  const renderIcon = (icon) => {
    switch (icon) {
      case "facebook":
        return <FaFacebookF />;
      case "twitter":
        return <FaTwitter />;
      case "youtube":
        return <FaYoutube />;
      case "linkedin":
        return <FaLinkedinIn />;
      default:
        return null;
    }
  };

  return (
    <footer className="bg-gray-900 text-white px-6 py-10">
      <div className="container mx-auto grid grid-cols-1 md:grid-cols-3 gap-8 border-b border-gray-700 pb-6">
        {/* Left Section */}
        <div>
          <img src={data.logo} alt="Logo" className="w-24 mb-4" />
          <p>{data.address}</p>
          <p>{data.phone}</p>
          <p>{data.email}</p>
          <div className="mt-2 space-y-1">
            {data.landlines.map((line, i) => (
              <p key={i}>{line}</p>
            ))}
          </div>
        </div>

        {/* Middle Section */}
        <div className="flex flex-wrap gap-6 md:col-span-2">
          {data.sections.map((section, i) => (
            <div key={i}>
              <h4 className="font-semibold mb-2">{section.title}</h4>
              <ul className="space-y-1">
                {section.links.map((link, j) => (
                  <li key={j}>
                    <a href={link.url} className="hover:text-gray-400">
                      {link.label}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </div>
      </div>

      {/* Bottom Bar */}
      <div className="container mx-auto flex flex-col md:flex-row justify-between items-center mt-6 text-sm">
        <div className="flex flex-wrap gap-4">
          {data.quickLinks.map((link, i) => (
            <a key={i} href={link.url} className="hover:text-gray-400">
              {link.label}
            </a>
          ))}
        </div>
        <div className="flex gap-4 mt-4 md:mt-0">
          {data.socials.map((s, i) => (
            <a
              key={i}
              href={s.url}
              className="w-8 h-8 flex items-center justify-center rounded-full bg-gray-800 hover:bg-gray-700"
            >
              {renderIcon(s.icon)}
            </a>
          ))}
        </div>
      </div>

      <div className="container mx-auto text-center mt-6 text-gray-500 text-xs">
        Â© Copyright 2024 - JSS. All Rights Reserved.
      </div>
    </footer>
  );
};

export default Footer;
